@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
html,
body,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  background: #efd592;
  height: 100vh;
  width: 100vw;
}

.App .section2 {
  -webkit-align-items: flex-start;
          align-items: flex-start;
  width: 50%;
  margin: 38px;
  background: #242d38;
}

.App .section1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 50%;
  color: #242d38;
}

.App .section1 h1 {
  font-size: 40px;
  margin-left: 20px;
  font-weight: 900;
  margin-top: 40px;
  font-family: 'Poppins', sans-serif;
}

.App .section1 p {
  font-size: 14px;
  margin-left: 20px;
  margin-top: 30px;
  width: 70%;
  font-weight: lighter;
  line-height: 25px;
  margin-bottom: 15px;
}

.App .section1 .read {
  letter-spacing: 3px;
  font-size: 14px;
  font-weight: 900;
}

@media (max-width: 620px) {
  .App {
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: auto;
  }
  .App .section2 {
    width: 100%;
    margin: 0;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: stretch;
            align-items: stretch;
  }
  .App .section1 {
    width: 100%;
    background: linear-gradient(#242d38 40%, #efd592 40% 60%);
  }
  .App .section1 h1 {
    font-size: 40px;
    margin-left: 20px;
    font-weight: 900;
    margin-top: 370px;
    font-family: 'Poppins', sans-serif;
  }
  .App .section1 p {
    font-size: 14px;
    margin-left: 20px;
    margin-top: 30px;
    font-weight: lighter;
    line-height: 40px;
    margin-bottom: 15px;
    width: 80%;
    margin-right: 20px;
  }
  .App .section1 .read {
    letter-spacing: 3px;
    font-size: 14px;
    font-weight: 900;
  }
}
